.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #93a4c5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(187, 33, 33);
}

.blue{
  color: #000000;
}

.App-link {
  color: #61dafb;
}

.App-button-box {
  border: 1px solid #ccc;
  padding: 5px;
  margin: 5px;
}

.App-balances {
  font-size: 0.7em;
}

.App-connection {
  font-size: 0.3em;
}

button { width: 150px; height: 30px; border-radius: 3px; background: #61dafb; color:#fffdfd; cursor: pointer; margin: 5px; border-radius: 99px; }
input[type=text] {width: 80px; height: 24px; text-align: center; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
